class MeteorRain {
    constructor() {
        this.x = -1;
        this.y = -1;
        this.length = -1;//
        this.angle = 30; //
        this.width = -1;//
        this.height = -1;//
        this.speed = 1;//
        this.offsetX = -1;//
        this.offsetY = -1;//
        this.alpha = 1; //
        this.color1 = "";//
        this.color2 = "";  //
    }

    //
    init() {
        this.getPos();
        this.alpha = 1;//
        this.getRandomColor();
        //
        let x = Math.random() * 80 + 100;
        this.length = Math.ceil(x);
        this.angle = 30; //
        x = Math.random() + 0.5;
        this.speed = Math.ceil(x); //
        let cos = Math.cos(this.angle * 3.14 / 180);
        let sin = Math.sin(this.angle * 3.14 / 180) ;
        this.width = this.length * cos ;  //
        this.height = this.length * sin ;//
        this.offsetX = this.speed * cos ;
        this.offsetY = this.speed * sin;
    }

    /*******************************/
    getRandomColor(){
        let a = Math.ceil(255 - 240 * Math.random());
        //
        this.color1 = "rgba("+ a + "," + a + "," + a + ",1)";
        //
        this.color2 = "black";
    }


    /*********************************/
    countPos() {
        //,xy
        this.x = this.x - this.offsetX;
        this.y = this.y + this.offsetY;
    }

    /**********************************/
    getPos() {
        //200--1200

        this.x = Math.random() * window.innerWidth; //
        //600
        this.y = Math.random() * window.innerHeight;  //
    }

    /*******************************/
    //
    draw(context) {
        context.save();
        context.beginPath();
        context.lineWidth = 1; //
        context.globalAlpha = this.alpha; //
        //,
        let line = context.createLinearGradient(this.x, this.y,
            this.x + this.width,
            this.y - this.height);



        //
        line.addColorStop(0, "white");
        line.addColorStop(0.3, this.color1);
        line.addColorStop(0.6, this.color2);
        context.strokeStyle = line;
        //
        context.moveTo(this.x, this.y);
        //
        context.lineTo(this.x + this.width, this.y - this.height);
        context.closePath();
        context.stroke();
        context.restore();
    }

    move(context) {
        //
        let x = this.x + this.width - this.offsetX;
        let y = this.y - this.height;
        context.clearRect(x - 3,y - 3,this.offsetX + 5,this.offsetY + 5);
        //
        this.countPos();
        //
        this.alpha -= 0.002;
        //
        this.draw(context);
    }
}

export default MeteorRain